#Footer {
    width: 100%;
    display: flex;
    background: #000000;
    box-shadow: 0px 0px 60px rgba(241, 203, 185, 0.22);
    flex-direction: row;
    min-height: 350px;
    margin: 0 auto;
    justify-content: flex-start;
    align-items: flex-start;
    z-index: 1000;
    overflow-y: hidden;

    .left {
        display: flex;
        flex-direction: column;
        width: 25%;
        margin-top: 10vh;
        margin-left: 150px;

    #logo_man {
        // position: relative;
        // top: 1.5%;
        // left: 13%;
        // width: 100%;
        // height: 100%;
        text-align: left;
        color: #FFFFFF;
        display: block;
        margin: 20px 0 10px 0;


        img {
            width: 15%;
            height: 15%;
            }
        }

        h4 {
            font-family: "Söhne Breit Test";
            font-size: 20px;
            line-height: 23px;
            text-transform: uppercase;
            white-space: nowrap;
            color: #FFFFFF;
        }

        p {
            font-family: "Söhne Breit Test";
            font-size: 12px;
            line-height: 15px;
            color: #FFFFFF;
            margin: 0.7vh;
            white-space: nowrap;
        }
    }

    .center {
        display: flex;
        margin-top: 12vh;
        align-items: center;
        justify-content: center;
        text-align: center;
        width: 30%;
        flex-direction: column;

        .row1 {
            display: flex;
            align-items: center;
            justify-content: center;
            text-align: center;

        a {
            margin-right: 1vw;
            border: 2px solid #FFFFFF;
            box-sizing: border-box;
            border-radius: 5px;
            align-items: center;
            justify-content: center;
            text-align: center;
            padding: 20px 20px;
            transition:  all 0.4s ease;
        }

        a:hover {
            background-color: #FFFFFF;
            border: 2px solid #3f03e6;

            img {
                filter: brightness(50%) sepia(1) hue-rotate(179.4deg  ) saturate(101.4%) brightness(0%);
            }
        }

        img {
            width: 30px;
            height: 30px;
        }
    }

    .row2 {
        p {
            margin: 40px;
            font-family: "Söhne Breit Test";
            font-size: 20px;
            line-height: 18px;
            text-align: center;
            color: #FFFFFF;
        }
        }
    }
    

    .right {
        width: 17%;
        display: flex;
        flex-direction: row;
        white-space: nowrap;
        margin-top: 8vh;
        position: absolute;
        right: 50px;
        
        .left_row {
            position: relative;
            top: 70px;
            left: -90px;
            display: flex;
            flex-direction: column;
        }
        h3 {
            font-family: "Söhne Breit Test";
            font-size: 18px;
            line-height: 45px;
            color: #FFFFFF;
            margin-bottom: 1vh;
        }

        .righ_row {
            position: relative;
            top: 70px;
            left: -50px;
            display: flex;
            flex-direction: column;
        }
        a {
            cursor: url("./../img/mouse1.png") 15 18, auto;
            color: #FFFFFF;
        }

        a:hover {
            cursor: url("./../img/mouse1.png") 15 18, auto;
            text-decoration:underline solid blue 3px ;
        }
        p {
            position: relative;
            font-family: "Söhne Breit Test";
            font-size: 12px;
            line-height: 18px;
            color: #FFFFFF;
            margin: 0.5vh;
        }
    }

}

