#Pack {
    position: relative;
    margin: 0 auto;
    // left: 350px;
    padding: 0px 0px 240px 0px;
    max-width: 900px;
    
    .center {
        height: 450px;
        display: flex;
     
        .left {
            border: 1px solid rgba(255, 255, 255, 0.2);
            width: 50%;
            // background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
            // box-shadow: -6px 6px 28px rgba(0, 0, 0, 0.47), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
            backdrop-filter: blur(37.5367px);
            /* Note: backdrop-filter has minimal browser support */

            .align {
                padding: 60px 60px 60px 60px;

            h2 {
                position: absolute;
                left: -100px;
                font-family: "Söhne Breit Test";
                // font-size: 35px;
                // line-height: 33px;
                font-size: 70px;
                line-height: 47px;
                text-transform: capitalize;
                font-weight: 500;
                margin: 0;
                color: #FFFFFF;
                transform: rotate(-90deg);
            }
            // h2 {
            //     font-family: "Söhne Breit Test";
            //     font-size: 35px;
            //     line-height: 33px;
            //     font-size: 35px;
            //     line-height: 33px;
            //     margin: 0;
            //     color: #FFFFFF;
            //     // transform: rotate(-90deg);
            // }
            p {
                font-family: "Founders Grotesk Condensed Test Regular";
                font-style: normal;
                // font-weight: 500;
                font-size: 22px;
                line-height: 23px;
                text-align: justify;
                color: white;
            }
        }
        }
        .left:hover {
            backdrop-filter: blur(0);
        }

        .right {
            width: 50%;
            // border-radius: 10px;
            background-image: url('./../img/fifa1.jpeg');
            // background-size: contain contain;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}

