header {
    font-size: 50px;
    line-height: 29px;
    font-family: "Söhne Breit Test";
    margin-bottom: 500px;
    text-align: center;

    .center {
      margin-top:40vh;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 50px;
      line-height: 50px;
      font-family: "Söhne Breit Test";
    //   animation: opac 1s linear forwards;
      
      .meta {
        display: flex;  
          h1 {
              color: transparent;
              -webkit-text-stroke-width: 1px;
              -webkit-text-stroke-color: #fff;
              margin: 2px;
              text-transform: uppercase;
              position: relative;
              display: block;
              opacity: 0;
            -webkit-animation-duration: 2s;
            -webkit-animation-timing-function: linear;
            -webkit-animation-iteration-count: 1;
            -webkit-animation-direction: normal;
            -webkit-animation-fill-mode: forwards;
            -webkit-animation-play-state: running;
            -webkit-animation-name: animate_opac;
            
            animation-duration: 2s;
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-direction: normal;
            animation-fill-mode: forwards;
            animation-play-state: running;
            animation-name: animate_opac;


            //   -webkit-animation: animate_opac 4s linear forwards;
            //   animation: animate_opac 4s linear forwards;

              
              $chars: 4;
              @for $i from 1 through $chars {
                  &:nth-of-type(#{$i}) {
                    -webkit-animation-delay: 0.3+0.1s*$i;
                    animation-delay: 0.3+0.1s*$i;
                  }
              }
            }
            }
    
    .Manager {
        margin-left: 40px;
        color: white;
        display: flex;
        // animation: opac 2s linear forwards;
        
        h1 {
            color: white;
            display: block;
            margin: 2px;
            opacity: 0;

            -webkit-animation-duration: 3s;
            -webkit-animation-timing-function: linear;
            -webkit-animation-iteration-count: 1;
            -webkit-animation-direction: normal;
            -webkit-animation-fill-mode: forwards;
            -webkit-animation-play-state: running;
            -webkit-animation-name: animate_opac;
            // -webkit-animation-name: opac;
            
            animation-duration: 3s;
            animation-timing-function: linear;
            animation-iteration-count: 1;
            animation-direction: normal;
            animation-fill-mode: forwards;
            animation-play-state: running;
            animation-name: animate_opac;

            // -webkit-animation: animate_opac 4s linear forwards;
            // animation: animate_opac 4s linear forwards;

            
            $chars: 7;
            @for $i from 1 through $chars {
                &:nth-of-type(#{$i}) {
                  -webkit-animation-delay: 0.2 +0.1s*$i;
                  animation-delay: 0.2+0.1s*$i;
                }
            }
        }
      }

      // @keyframes animate_opac {
      //     0% {
      //         filter: blur(0);
      //         opacity: 0;
      //       }
            
      //       40% {
      //           filter: blur(20px);
      //           opacity: 0;
