      //       80% {
      //           filter: blur(0);
      //           opacity: 0.5;
      //       }
            
      //       100% {
      //           filter: blur(0);
      //           opacity: 1;
      //       }
      //   }
      //   @-webkit-keyframes animate_opac {
      //     0% {
      //       -webkit-filter: blur(0);
      //       -webkit-opacity: 0;
      //     }
  
      //     40% {
      //       -webkit-filter: blur(20px);
      //       -webkit-opacity: 0;
      //     }
  
      //     80% {
      //       -webkit-filter: blur(0);
      //       -webkit-opacity: 0.5;
      //     }
  
      //     100% {
      //       -webkit-filter: blur(0);
      //       -webkit-opacity: 1;
      //     }
      // }
    }
  }
  
  @media only screen and (max-width: 1200px) {

    header {
        // margin-bottom: 350px;

      .center {
        // margin-top:350px;
        font-size: 43px;
        line-height: 43px;

      }
    }
}

  @media only screen and (max-width: 1000px) {

    header {
        margin-bottom: 350px;

      .center {
        // margin-top:400px;
        font-size: 37px;
        line-height: 30px;

      }
    }
}

@media only screen and (max-width: 850px) {

    header {
      .center {
        margin-top:320px;
        font-size: 35px;
        line-height: 40px;
        flex-direction: column;

        .Manager {
            margin-left: 0;
            margin: 20px;
        }
      }
    }
}

@media only screen and (max-width: 600px) {

    header {
      .center {
        margin-top:270px;
        font-size: 25px;
        line-height: 25px;
        flex-direction: column;
        
        .meta {
            h1 {
                margin: 1px;
                -webkit-animation-duration: 3s;
                -webkit-animation-timing-function: linear;
                -webkit-animation-iteration-count: 1;
                -webkit-animation-direction: normal;
                -webkit-animation-fill-mode: forwards;
                -webkit-animation-play-state: running;
                -webkit-animation-name: animate_opac;
                
                animation-duration: 3s;
                animation-timing-function: linear;
                animation-iteration-count: 1;
                animation-direction: normal;
                animation-fill-mode: forwards;
                animation-play-state: running;
                animation-name: animate_opac;
    
                  $chars: 4;
                  @for $i from 1 through $chars {
                      &:nth-of-type(#{$i}) {
                        -webkit-animation-delay: 1 +0.1s*$i;
                        animation-delay: 1+0.1s*$i;
                      }
                  }
            }
        }
        
        .Manager {
            margin-left: 0;
            margin: 20px;
            
            h1 {
                margin: 1px;
                -webkit-animation-duration: 3s;
                -webkit-animation-timing-function: linear;
                -webkit-animation-iteration-count: 1;
                -webkit-animation-direction: normal;
                -webkit-animation-fill-mode: forwards;
                -webkit-animation-play-state: running;
                -webkit-animation-name: animate_opac;

                
                animation-duration: 3s;
                animation-timing-function: linear;
                animation-iteration-count: 1;
                animation-direction: normal;
                animation-fill-mode: forwards;
                animation-play-state: running;
                animation-name: animate_opac;

                $chars: 7;
                @for $i from 1 through $chars {
                    &:nth-of-type(#{$i}) {
                      -webkit-animation-delay: 2+0.1s*$i;
                      animation-delay: 2+0.1s*$i;
                    }
                }
            }
          }
      }
    }
}
