#artiste {
    position: relative;
    padding: 120px 0px 180px 0px;
    width: 100%;
    background-color: #080101;
    overflow: hidden;
    background-image: url("./../img/artiste_back.png");
    background-position: bottom right 150px;
    background-repeat: no-repeat;
    background-size: 60% 60%;

    .light {
        width: 200px;
        margin: 100px 0;
        height: 200px;
        background: rgb(90,38,245);
        position: absolute;
        left: -100px;
        filter: blur(100px);
    }
    .title {
        // padding-top: 85px;
        text-align: center;
        margin-bottom: 100px;

        h1 {
            font-family: "Söhne Breit Test";
            font-size: 44px;
            line-height: 44px;
            text-align: center;
            display: inline-block;
          }

        h1::before {
            position: relative;
            display: inline-block;
            content: "";
            top: -13px;
            left: -28px;
            // background-image: url('./../img/button.png');
            background: linear-gradient(to top, rgb(93, 93, 223), rgb(39, 39, 250));
            background-size: cover;
            width: 12px;
            height: 12px;
            transform: rotate(-135deg);
        }
    
        h1:hover::before {
            border: 2px solid #FFFFFF;
            background: transparent;
            transition: all 0.5s ease-out;
            // animation: animate 1s linear forwards;
            -webkit-box-shadow: 0px 0px 0px 2px rgb(39, 39, 250); 
            box-shadow: 0px 0px 0px 2px rgb(39, 39, 250);
        }
    
        h1::after {
            position: relative;
            display: inline-block;
            content: "";
            top: -13px;
            right: -28px;
            // background-image: url('./../img/button.png');
            background: linear-gradient(to top, rgb(93, 93, 223), rgb(39, 39, 250));
            background-size: cover;
            width: 12px;
            height: 12px;
            transform: rotate(-135deg);
        }
        h1:hover::after {
            border: 2px solid #FFFFFF;
            background: transparent;
            transition: all 0.5s ease-out;
            // animation: animate 1s linear forwards;
            -webkit-box-shadow: 0px 0px 0px 2px rgb(39, 39, 250); 
            box-shadow: 0px 0px 0px 2px rgb(39, 39, 250);
        }
    }

    .container {
        height: 500px;
        width: 830px;
        margin: 0px 28vw;
        // display: flex;
        background-color: transparent;
        // background-image: url("./../img/back_artsite.png");
        // background-position: bottom right;
        // background-repeat: no-repeat;
        // background-size: cover;
        // box-shadow: rgba(7, 7, 105, 0.25) 0px 60px 150px -12px inset, rgba(41, 9, 114, 0.3) 0px 208px 76px -18px inset;
        // flex-direction: column;
        border-radius: 10px;
        z-index: 20;

        hr {
            width: 100%;
            margin-top: 40px;
            height:0.1px;
            color: #D9D9D9;
            position: absolute;
            left: 0px;
          }

        .left::before {
            display: inline-block;
            content: "";
            position: absolute;
            width: 407px;
            height: 446px;
            border-radius: 6px;
            margin-top: 90px;
            left: -80px;
            // border-radius: 10px;
            background-image: url('./../img/Perso.png');
            // background-size: contain contain;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
        }

        
        @supports (-webkit-backdrop-filter: none) or (backdrop-filter: none) {
            .left {
                -webkit-backdrop-filter: blur(10px);
              backdrop-filter: blur(10px);
            //   background: linear-gradient(85.96deg, rgba(255, 255, 255, 0) -20.51%, rgba(255, 255, 255, 0.05) 26.82%, rgba(255, 255, 255, 0) 65.65%), rgba(196, 196, 196, 0.05);
            //   background-color: rgba(170, 170, 170, 0.658);  
            }
            .warning {
              display: none;
            }
        }
          
        .left {
            display: flex;
            flex-direction: column;
            padding: 0px 50px 0px 370px;
            // z-index: 200;
            // backdrop-filter: blur(100.5367px);
            // background-color: #080101;
            // box-shadow: rgba(7, 7, 105, 0.25) 0px 60px 150px -12px inset, rgba(41, 9, 114, 0.3) 0px 208px 76px -18px inset;
            height: 100%;
            position: relative;
            box-shadow: -6px 6px 28px rgba(0, 0, 0, 0.47), inset 0px -2.50245px 1.25122px rgba(255, 255, 255, 0.1);
            background: linear-gradient(5.96deg, rgba(59, 53, 53, 0) -0.51%, rgba(117, 115, 115, 0.05) 26.82%, rgba(85, 84, 84, 0.61) 95.65%), rgba(0, 0, 0, 0.05);
            // background: linear-gradient(5.96deg, rgba(14, 19, 29, 0) -0.51%, rgba(4, 3, 31, 0.05) 26.82%, rgba(40, 32, 151, 0.62) 99.65%), rgba(10, 70, 233, 0.05);
 

            h2 {
                font-family: "Söhne Breit Test";
                font-size: 30px;
                line-height: 28px;
                margin: 0; 
                margin-top: 80px;
                color: #FFFFFF;   
            }
            h1 {
                font-family: "Söhne Breit Test";
                font-size: 50px;
                line-height: 50px;
                text-align: center;
                display: inline-block;
              }

              #twitter_jo {
                font-family: "Founders Grotesk Condensed Test Regular";
                font-style: normal;
                font-weight: 500;
                font-size: 22px;
                line-height: 23 px;
                text-align: justify;
                color: #ffffff;
                margin-top: 15px;
            
                img {
                //   margin: -2% 6%;
                  text-align: right;
                }      
              }

            p {
                font-family: 'Founders Grotesk Condensed Test Regular';
                font-style: normal;
                font-weight: 500;
                font-size: 24px;
                line-height: 26px;
                color: #959595;
                margin: 7px 0 40px 0;
                // margin-bottom: 40px;
            }
            h3 {
                font-family: "Founders Grotesk Condensed Test Regular";
                font-style: normal;
                font-weight: 500;
                font-size: 22px;
                line-height: 23px;
                text-align: justify;
                color: #ffffff;
            }
        }
    }
}

@media only screen and (max-width: 1600px) {
    #artiste {
        position: relative;
        padding: 120px 0px 180px 0px;
        width: 100%;
        background-color: #080101;
        overflow: hidden;
        background-image: url("./../img/artiste_back.png");
        background-position: bottom right -50px;
        background-repeat: no-repeat;
        background-size: 70% 60%;
    }
  }


@media only screen and (max-width: 1250px) {
    #artiste {
        .container {
            margin: 0px auto;
        }
    }
}

@media only screen and (max-width: 1100px) {
    #artiste {
        background-position: bottom right -50px;
        background-size: 70% 50%;
        // padding: 180px 0px 180px 40px;
        .container::after {
            display: none;
        }

        .container::before {
            display: none;
        }

        .container {
            height: auto;
            width: 670px;
            margin: 0px auto;
            // margin-right: 2px;

            .left::before {
                width: 287px;
                height: 336px;
                margin-top: 90px;
                left: -45px;
            }

            
            .left {
                padding: 0px 50px 0px 285px;

                p {
                    margin: 7px 0 15px 0;
                }

                #twitter_jo {
                    margin-top: 15px;
                    margin-bottom: 5vh;
                  }
            }
        }
    }
}

