  #meta_manager {
    position: fixed;
    z-index: 99;
    top: 0%;
    left: 2%;
    font-family: "Söhne Breit Test";
    font-weight: 600;
    font-size: 25px;
    line-height: 29px;
    // padding: 20px 30px;
    color: #FFFFFF;
    opacity: 0;
    display: block;

        -webkit-animation-duration: 5s;
    -webkit-animation-timing-function: ease-out;
    -webkit-animation-delay: 0s;
    -webkit-animation-iteration-count: 1;
    -webkit-animation-direction: normal;
    -webkit-animation-fill-mode: forwards;
    -webkit-animation-play-state: running;
    -webkit-animation-name: animate_opac;

    animation-duration: 5s;
    animation-timing-function: ease-out;
    animation-delay: 0s;
    animation-iteration-count: 1;
    animation-direction: normal;
    animation-fill-mode: forwards;
    animation-play-state: running;
    animation-name: animate_opac;
  }

  @keyframes animate_opac {
    0% {
        filter: blur(0);
        opacity: 0;
    }
  
    40% {
        filter: blur(20px);
        opacity: 0;
    }
  
    80% {
        filter: blur(0);
        opacity: 0.5;
    }
  
    100% {
        filter: blur(0);
        opacity: 1;
    }
  }

  @-webkit-keyframes animate_opac {
  0% {
    -webkit-filter: blur(0);
    -webkit-opacity: 0;
  }

  40% {
    -webkit-filter: blur(20px);
    -webkit-opacity: 0;
  }

  80% {
    -webkit-filter: blur(0);
    -webkit-opacity: 0.5;
  }

  100% {
    -webkit-filter: blur(0);
    -webkit-opacity: 1;
  }
}

  #cta {
    position: fixed;
    width: 140px;
    white-space: nowrap;
    z-index: 99;
    top: 20px;
    right: 50px;
    font-family: "Söhne Breit Test";
    font-size: 16px;
    line-height: 19px;
    padding: 18px;
    color: #ffffff;
    font-weight: bold;
    background-image: url('./../img/button.png');
    background-size: cover;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.94) inset;
    transition: 1s all ease;
    opacity: 0;
    display: block;
    -webkit-animation-duration: 5s;
    -webkit-animation-timing-function: ease-out;
    -webkit-animation-delay: 0s;
    -webkit-animation-fill-mode: forwards;
    -webkit-animation-name: animate_opac;

    animation-duration: 5s;
    animation-timing-function: ease-out;
    animation-delay: 0s;
    animation-fill-mode: forwards;
    animation-name: animate_opac;
  }

  #cta::before {
    color: black;
    content: '';
    position: absolute;
    top: 50%;
    z-index: -1;
    left: 50%;
    transform: translate(-50%, -50%);
    transition:  all 0.3s ease;
    width: 0%;
    height: 100%;

  }

  #cta:hover::before {
    width: 100%;
    color: black;
    background-color: white;
    z-index: -1;
  }

  #cta:hover {
    color:#001AFF;
  }

  #twitter {
