  #twitter::before {
    color: black;
    content: '';
    background-color: white;
    position: absolute;
    top: 50%;
    z-index: -1;
    left: 50%;
    transform: translate(-50%,-50%);
    transition:  all 0.4s ease;
    width: 120%;
    height: 0%;
    transform: translate(-50%,-50%)rotate(25deg);
  }
  #twitter:hover::before {
    height: 215%;
    color: black;
    background-color: white;
    z-index: -1;
  }
  
  #twitter:hover {
    color: black;

    img {
      filter: brightness(50%) sepia(1) hue-rotate(179.4deg  ) saturate(101.4%) brightness(0%);
    }
  }
  
