    animation-timing-function: ease-out;
    animation-delay: 0s;
    animation-fill-mode: forwards;
    animation-name: animate_opac;
  }

  #drop_soon::after {
    z-index: -1;
    position: absolute;
    display: inline-block;
    content: "";
    top: -40px;
    left: -10px;
    // background-image: url('./../img/data_carre.png');
    background: linear-gradient(to top, rgb(39, 39, 250), rgb(93, 93, 223));
    // background-size: contain;
    width: 75px;
    height: 75px;
    // width: 130px;
    // height: 130px;
    transform: rotate(45deg);
    }

    #drop_soon:hover::after {
      border: 2px solid #FFFFFF;
      background: transparent;
      animation: animate 1s linear forwards;
      -webkit-box-shadow: 0px 0px 0px 5px rgb(39, 39, 250); 
      box-shadow: 0px 0px 0px 5px rgb(39, 39, 250);
    }

