@keyframes opac {
  0% {
      opacity: 0;
  }
  50% {
      opacity: 0;
  }
  75% {
      opacity: 0.5;
  }
  100% {
      opacity: 1;
  }
}

@-webkit-keyframes opac {
  0% {
    -webkit-opacity: 0;
  }
  50% {
    -webkit-opacity: 0;
  }
  75% {
    -webkit-opacity: 0.5;
  }
  100% {
    -webkit-opacity: 1;
  }
}
.scroll-downs {
    position: absolute;
    // top: 0;
    // right: 0;
    bottom: 10%;
    left: 50%;
    // margin: 0 auto;
    
    width :34px;
    height: 55px;

    .scroll {
      display: unset;
    }

    .swipe {
      display: none;
    }

    h3 {
        position: absolute;
        // left: ;
        bottom: -40px;
        left: -30px;
        // left: 200px;
        font-family: "Founders Grotesk Condensed Test Regular";
        // opacity: 0;
        font-style: normal;
        // width: 80%;
        font-weight: 100;
        font-size: 20px;
        line-height: 20px;
        text-align: justify;
        color: #E5E5E5;
        white-space: nowrap;
    }
  }
  .mousey {
    width: 3px;
    padding: 10px 12px;
    height: 25px;
    border: 2px solid #fff;
    border-radius: 25px;
    opacity: 0.75;
    box-sizing: content-box;
  }
  .scroller {
    width: 3px;
    height: 10px;
    border-radius: 25%;
    background-color: #fff;
    animation-name: scroll;
    animation-duration: 2.2s;
    animation-timing-function: cubic-bezier(.15,.41,.69,.94);
    animation-iteration-count: infinite;
  }
  @keyframes scroll {
    0% { opacity: 0; }
    10% { transform: translateY(0); opacity: 1; }
    100% { transform: translateY(15px); opacity: 0;}
  }

  @keyframes scrollY {
      from {
          opacity: 1;
      }
      to {
          opacity: 0;
      }
  }

  @media only screen and (max-width: 600px) {
    .scroll-downs {
        bottom: 0;
        left: 0;
        right: 0;
        top: 0;
        width : 100%;
        height: 100%;
        opacity: 0;
        display: block;

        -webkit-animation-duration: 5.5s;
        -webkit-animation-delay: 2.5s;
        -webkit-animation-timing-function: ease-out;
        -webkit-animation-iteration-count: 1;
        -webkit-animation-direction: normal;
        -webkit-animation-fill-mode: forwards;
        -webkit-animation-play-state: running;
        -webkit-animation-name: opac;
    
        animation-duration: 5.5s;
        animation-delay: 2.5s;
        animation-timing-function: ease-out;
        animation-iteration-count: 1;
        animation-direction: normal;
        animation-fill-mode: forwards;
        animation-play-state: running;
        animation-name: opac;
        // -webkit-animation: animate_opac 5.5s ease-out forwards 2.5s;
        // animation: animate_opac 5.5s ease-out forwards 2.5s;
    
        .scroll {
          display: none;
        }
        
        .swipe {
          display: unset;
        }

        h3 {
            bottom: 12%;
            left: 40%;
            font-size: 20px;
            line-height: 20px;
        }
      }

      .mousey::before {
          content: "";
          position: absolute;
          display: inline-block;
          top: -10px;
          left: -10px;
          width: 20px;
          padding: 0px 0px;
          height: 20px;
          border: 1px solid #fff;
          border-top: transparent;
          border-left: transparent;
          animation: pulse 3s linear infinite;
      }

      .mousey {
        position: absolute;
        left: 50%;
        bottom: 9.5%;
        width: 20px;
        padding: 0px 0px;
        height: 20px;
        border: 1px solid #fff;
        border-radius: 0px;
        transform: rotate(45deg);
        border-top: transparent;
        border-left: transparent;
        opacity: 1;
        box-sizing: content-box;
        animation: pulse 3s linear infinite;
      }

      @keyframes pulse {
        0% {
            opacity: 1;
        }
        40% {
         
            opacity: 0.75;
        }
        75% {
            opacity: 0.25;
        }
        100% {
            opacity: 1;
        }
    }
      .scroller {
          display: none;
      }
  }